.HistoryTagWrapper {
  position: relative;
  height: 30vh;
  width: 25vw;
  min-width: 320px;
  border-radius: 20px;
  box-shadow: 0px 5px 15px 0.5px #edeffc;
  transition-property: color, box-shadow;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
  background: white;
  margin: 15px;
  color: #ccc;
  cursor: pointer;
  font-size: 11em;
  overflow: hidden;
  user-select: none;
}

.Background {
  position: absolute;
  background: linear-gradient(
    35deg,
    rgba(89, 39, 229, 1) 0%,
    rgba(89, 39, 229, 1) 27%,
    rgba(148, 110, 255, 1) 100%
  );
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  z-index: 9;
  border-radius: 20px;
}

.HistoryTagWrapper:hover .Background,
.HistoryTagWrapper:active .Background,
.HistoryTagWrapper.active .Background {
  opacity: 1;
}

.HistoryTagWrapper:hover,
.HistoryTagWrapper:active,
.HistoryTagWrapper.active {
  color: white;
  box-shadow: 0px 5px 10px 1px rgba(89, 39, 229, 0.5);
}
.HistoryTagWrapper:hover .OrderNumber,
.HistoryTagWrapper:active .OrderNumber,
.HistoryTagWrapper.active .OrderNumber {
  color: rgba(255, 255, 255, 0.5);
}

.OrderNumber {
  position: absolute;
  bottom: 0;
  right: 0;
  color: #ccc;
  font-weight: 800;
  transition-property: color;
  transition-duration: 0.3s;
  transition-timing-function: ease-in-out;
  line-height: 0.4em;
  z-index: 10;
}

.Title {
  position: absolute;
  font-size: 20px;
  margin: 5%;
  z-index: 10;
}

.TagImg {
  position: absolute;
  z-index: 11;
  filter: brightness(0) invert(1);
}

.Signal {
  position: absolute;
  height: 22%;
  z-index: 11;
  filter: brightness(0) invert(1);
}