.Header {
  display: flex;
  text-align: center;
  height: 100vh;
  width: 100vw;
  align-items: center;
  justify-content: center;
  z-index: 10;
  white-space: pre;
  user-select: none;
}

.Header h1 {
  position: absolute;
  height: fit-content;
  display: flex;
  top: 0;
  left: 0;
  margin: 2%;
}

.Introduction {
  position: absolute;
  height: fit-content;
  display: flex;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3em;
  font-weight: 800;
}

.Mouse {
  opacity: 0.5;
  width: 25px;
  height: 45px;
  border: 3px solid #333;
  border-radius: 60px;
}
.Mouse::before {
  content: "";
  width: 6px;
  height: 6px;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #333;
  border-radius: 50%;
  opacity: 1;
  animation: wheel 2s infinite;
  -webkit-animation: wheel 2s infinite;
}

@keyframes wheel {
  to {
    opacity: 0;
    top: 30px;
  }
}

@keyframes wheel-mobile {
  to {
    opacity: 0;
    top: 0px;
  }
}

@media (max-width: 1224px) and (orientation: portrait) {
  .Header {
    font-size: small;
  }
  .Mouse {
    border: 0px;
  }
  .Mouse::before {
    top: 30px;
    width: 20px;
    height: 20px;
    animation: wheel-mobile 2s infinite;
    -webkit-animation: wheel-mobile 2s infinite;
  }
}