.AQIWrapper {
  position: absolute;
  border-radius: 10px;
  box-shadow: 2px 2px 10px #ccc;
  width: 100%;
  height: 100%;
  font-size: 3.5vw;
}

.AQIValue {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 10px;
  font-weight: bold;
}

.AQILocation {
  position: absolute;
  bottom: 0;
  right: 0;
  margin: 13px;
  font-size: 1vw;
}
