.NewHorizon {
  flex-direction: row;
  align-items: center;
}

.BeautifulLogo {
  text-align: center;
  width: 15%;
  height: 15%;
}

.SmallLogo {
  width: 60px;
}

.SmallHistoryDiv {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  top: 55%;
  left: 60px;
  width: 100%;
}

.InputField {
  width: 80%;
}

.Icons {
  margin-right: 50px;
  color: black;
  transition: color 0.3s ease-in-out;
}

.Icons:hover,
.Icons:active,
.Icons.active {
  color: rgb(89, 39, 239);
}