.History1 {
  flex-direction: row;
  align-items: center;
}

.History1 img {
  width: 15%;
  height: 15%;
  max-width: 200px;
}

.SmallLogo {
  width: 60px;
}

.SmallHistoryDiv {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  top: 5%;
  left: 60px;
  width: 100%;
}

.ImgDesc {
  position: absolute;
  left: 50%;
  bottom: 2%;
  transform: translateX(-50%);
  background: linear-gradient(
    35deg,
    rgba(89, 39, 229, 0.75) 0%,
    rgba(89, 39, 229, 0.75) 27%,
    rgba(148, 110, 255, 0.75) 100%
  );
  padding: 5px;
  border-radius: 5px;
  color: white;
  user-select: none;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  text-align: center;
}

.ImgDiv:hover .ImgDesc,
.ImgDiv:active .ImgDesc,
.ImgDiv.active .ImgDesc {
  opacity: 1;
}