@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px) rotate(360deg);
  }
}

.Background {
  position: fixed;
  width: 100vw;
  height: 140vh;
  top: 0;
  left: 0;
  background: #fbfbfb;
  overflow: hidden;
  z-index: -100;
  opacity: 0.7;
}

.Background span {
  width: 32vmin;
  height: 32vmin;
  border-radius: 32vmin;
  backface-visibility: hidden;
  position: absolute;
  animation: move;
  animation-duration: 45;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transition: all 0.3s ease-in-out;
}

.Background span:nth-child(0) {
  top: 56%;
  left: 6%;
  animation-duration: 25s;
  animation-delay: -25s;
  transform-origin: 22vw -22vh;
  box-shadow: 64vmin 0 8.373995869661252vmin currentColor;
}
