.History3 {
  flex-direction: row;
  align-items: center;
}

.History3 img {
  width: 15%;
  height: 15%;
}

.SmallLogo {
  width: 60px;
}

.SmallHistoryDiv {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  top: 47%;
  left: 60px;
  width: 100%;
}

.Tag1 {
  position: relative;
  width: 90%;
  height: 43.5px;
  margin: 0 5% 2%;
}
.Tag1:first-of-type {
  margin: 5% 5% 2%;
}

.AQI {
  user-select: none;
  position: relative;
  width: 30%;
  padding-bottom: 30%;
  height: 0;
  margin: 2% 5% 0%;
}

.CircularPB {
  width: 30%;
  height: 30%;
  margin: 2% 5% 0%;
}

.Buildings {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 40px;
  word-wrap: break-word;
  font-size: 40px;
  padding: 20px;
  z-index: 110;
}

.ImgDesc {
  position: absolute;
  left: 50%;
  bottom: 2%;
  transform: translateX(-50%);
  background: linear-gradient(
    35deg,
    rgba(89, 39, 229, 0.75) 0%,
    rgba(89, 39, 229, 0.75) 27%,
    rgba(148, 110, 255, 0.75) 100%
  );
  padding: 5px;
  border-radius: 5px;
  color: white;
  user-select: none;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  text-align: center;
}

.ImgDiv:hover .ImgDesc,
.ImgDiv:active .ImgDesc,
.ImgDiv.active .ImgDesc {
  opacity: 1;
}